<template>
  <div class="goodsattribute">
    <div id="leixin">
      <div class="search_container">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label="病象名称" size="mini">
            <el-select @focus="getNEwlist" clearable v-model="goodsattribute.symptom_name" filterable remote placeholder="请输入">
              <el-option :label="item.symptom_name" v-for="item in diseaseList" :key="item.symptom_id" :value="item.symptom_name">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="助记码" size="mini">
            <el-input v-model="goodsattribute.logogram" placeholder="请输入"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="add_delet">
        <el-button icon="el-icon-circle-plus-outline" type="primary" @click="dialogFormVisible = true" class="addBtn" size="mini" v-if="authority.b_add==1">新增</el-button>
        <el-button plain icon="el-icon-delete" @click="deletes" size="mini" v-if="authority.b_del==1">删除</el-button>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" fit v-loading="loading1" style="width: 100%" @selection-change="handleSelectionChange" max-height="600">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="symptom_id" label="操作" fixed="right" width="54">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.$index, scope.row)" type="text" size="small" v-if="authority.b_edit==1">
                修改
              </el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="symptom_id" width="170" label="病象ID">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="symptom_name" width="170" label="病象名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="logogram" label="助记码">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" prop="remark" width="170" label="备注">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" prop="describe" label="描述">
          </el-table-column>

        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>

      <el-dialog :close-on-click-modal="false" title="添加/修改病象管理" @open="open" center :visible.sync="dialogFormVisible" width="450px">
        <el-form :model="ruleForm" ref="ruleForm">
          <el-form-item label="名称" class="errorItem errorItemMl121" prop="symptom_name" :rules="[{ required: true, message: '请输入名称', trigger: 'blur' }]">
            <el-input v-model="ruleForm.symptom_name" :disabled="ruleForm.is_system==1" maxlength="50"></el-input>
          </el-form-item>
          <el-form-item label="备注">
            <el-input type="text" maxlength="300" :disabled="ruleForm.is_system==1" v-model="ruleForm.remark" placeholder="限制输入300字符"></el-input>
          </el-form-item>
          <el-form-item label="描述">
            <el-input type="text" maxlength="300" v-model="ruleForm.describe" placeholder="限制输入300字符"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogFormsubmit('ruleForm')">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "aftersaleSymptom",
  data() {
    return {
      goodsattribute: {},
      searchParams: {}, //参数传递值

      select_option: "", //下拉框选中项
      dialogFormVisible: false,
      loading:false,
      ruleForm: {
        symptom_name: "",
        remark: ""
      },
      options: {
        options: []
      }
    };
  },
  methods: {
    onReset() {
      /** 重置时 如需保存默认参数，则： */
      this.$set(this, "goodsattribute", {});
      this.$set(this, "searchParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getDiseaseList", //接口名
        vm: this //传递的this
      });
      getData(this.searchParams);
    },
    handleEdit(index, row) {
      //   点击修改
      this.dialogFormVisible = true;
      this.$nextTick(function() {
        this.ruleForm = {
          symptom_name: row.symptom_name,
          describe: row.describe,
          remark: row.remark,
          symptom_id: row.symptom_id,
          is_system: row.is_system
        };
      });
    },
    onSearch() {
      //查询
      Object.assign(this.searchParams.params, this.goodsattribute, {
        page: 1
      });
      getData(this.searchParams);
    },
    deletes() {
      var deledata = {
        deleID: "symptom_id",
        deleSelect: this.multipleSelection,
        deleApi: "delDisease",
        list: {
          UrlApi: "getDiseaseList",
          data: this.TableData
        }
      };
      this.$common.deletes(deledata, "删除", this);
    },
    open() {
      this.ruleForm = {};
    },
    dialogFormsubmit(ruleForm) {
      var This = this;
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          if (This.ruleForm.symptom_id == undefined) {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "addDisease",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getDiseaseList",
                data: this.TableData,
                esData: {
                  page: this.goPage
                }
              }
            };
            this.$common.addAndupdateFun(funcan);
          } else {
            let funcan = {
              eachData: this.ruleForm,
              UrlApi: "editDisease",
              closeDialog: (this.dialogFormVisible = false),
              list: {
                UrlApi: "getDiseaseList",
                data: this.TableData,
                esData: {
                  page: this.goPage
                }
              }
            };
            this.$common.addAndupdateFun(funcan);
          }
        } else {
          console.error("error submit!!");
          return false;
        }
      });
    }
  },
  mounted() {
    this.$set(this, "searchParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getDiseaseList", //接口名
      vm: this //传递的this
    });
    getData(this.searchParams);
  }
};
</script>
